<template>
  <div class="result-display" v-if="result">
    <h2>八字命理分析结果</h2>
    <div class="result-content">
      <div class="user-info">
        <h3>基本信息</h3>
        <p><strong>姓名：</strong>{{ userInfo.name }}</p>
        <p><strong>出生：</strong>{{ formatDateTime(userInfo.birthDate, userInfo.birthTime) }}</p>
        <p><strong>出生地：</strong>{{ userInfo.birthCity }}</p>
      </div>
      
      <div class="analysis-result">
        <div v-html="formattedResult"></div>
      </div>

      <button class="back-btn" @click="$emit('back')">返回修改</button>
    </div>
  </div>

  <div v-else class="loading">
    <div class="loading-spinner"></div>
    <p>正在分析命理，请稍候...</p>
  </div>
</template>

<script setup>
import { computed } from 'vue'
import dayjs from 'dayjs'

const props = defineProps({
  result: {
    type: String,
    default: ''
  },
  userInfo: {
    type: Object,
    required: true
  }
})

defineEmits(['back'])

const formatDateTime = (date, time) => {
  if (!date || !time) return ''
  return dayjs(`${date} ${time}`).format('YYYY年MM月DD日 HH:mm')
}

const formattedResult = computed(() => {
  if (!props.result) return ''
  // 将换行符转换为HTML换行
  return props.result.replace(/\n/g, '<br>')
})
</script>

