<template>
  <div class="user-info-form" v-if="!showResult">
    <h2>八字命理分析</h2>
    <form @submit.prevent="handleSubmit" class="form-container">
      <!-- 姓名 -->
      <div class="form-group">
        <label for="name">姓名</label>
        <input
          id="name"
          v-model="formData.name"
          type="text"
          required
          placeholder="请输入您的姓名"
        >
      </div>

      <!-- 性别 -->
      <div class="form-group">
        <label>性别</label>
        <div class="radio-group">
          <label class="radio-label">
            <input
              type="radio"
              v-model="formData.gender"
              value="male"
              required
            >
            男
          </label>
          <label class="radio-label">
            <input
              type="radio"
              v-model="formData.gender"
              value="female"
            >
            女
          </label>
        </div>
      </div>

      <!-- 出生日期 -->
      <div class="form-group">
        <label for="birthDate">出生日期</label>
        <input
          id="birthDate"
          v-model="formData.birthDate"
          type="date"
          required
          :max="maxDate"
        >
      </div>

      <!-- 出生时间 -->
      <div class="form-group">
        <label for="birthTime">出生时间</label>
        <input
          id="birthTime"
          v-model="formData.birthTime"
          type="time"
          required
          step="60"
        >
      </div>

      <!-- 出生城市 -->
      <div class="form-group">
        <label for="birthCity">出生城市</label>
        <input
          id="birthCity"
          v-model="formData.birthCity"
          type="text"
          required
          placeholder="请输入出生城市"
        >
      </div>

      <!-- 婚姻状况 -->
      <div class="form-group">
        <label for="maritalStatus">婚姻状况</label>
        <select
          id="maritalStatus"
          v-model="formData.maritalStatus"
          required
        >
          <option value="">请选择</option>
          <option value="single">未婚</option>
          <option value="married">已婚</option>
          <option value="divorced">离异</option>
          <option value="widowed">丧偶</option>
        </select>
      </div>

      <button type="submit" class="submit-btn" :disabled="isLoading">
        {{ isLoading ? '分析中...' : '开始分析' }}
      </button>

      <div v-if="error" class="error-message">
        {{ error }}
      </div>
    </form>
  </div>

  <ResultDisplay
    v-else
    :result="analysisResult"
    :user-info="formData"
    @back="showResult = false"
  />
</template>

<script setup>
import { ref, computed } from 'vue'
import dayjs from 'dayjs'
import ResultDisplay from './ResultDisplay.vue'

const formData = ref({
  name: '',
  gender: '',
  birthDate: '',
  birthTime: '',
  birthCity: '',
  maritalStatus: ''
})

const isLoading = ref(false)
const error = ref('')
const showResult = ref(false)
const analysisResult = ref('')

// 计算最大日期为今天
const maxDate = computed(() => {
  return dayjs().format('YYYY-MM-DD')
})

const handleSubmit = async () => {
  try {
    isLoading.value = true
    error.value = ''

    // 构建请求数据
    const requestData = {
      model: "gpt-4o-mini",
      messages: [
        {
          role: "system",
          content: `你是一位精通中国传统命理的顶尖大师，具备以下专业素养：

1. 命理理论基础：
- 精通八字排盘、奇门遁甲、紫微斗数、六爻等传统命理体系
- 深入研究《穷通宝典》《三命通会》《滴天髓》《渊海子平》等经典著作
- 掌握子平术、星宗推演、十神定位和五行生克制化的核心理论

2. 专业分析能力：
- 能准确解读八字命盘中的天干地支关系
- 精通日主强弱判断、格局分析、用神取用
- 擅长分析命主先天禀赋、性格特征和人生规律
- 能结合大运流年进行准确的运势预测

3. 个性化分析方法：
- 根据用户年龄特征（18岁、30岁、40岁、60岁）提供定制化建议
- 结合现代职业发展特点进行事业指导
- 针对不同婚姻状况提供感情分析和建议
- 融合传统养生理论提供健康指导

4. 分析原则：
- 遵循"趋吉避凶"的原则，引导命主扬长避短
- 保持客观中立，不做过分夸大或诱导性判断
- 注重实用性建议，帮助命主趋利避害
- 分析内容应包含事业、财运、姻缘、健康等各个方面

5. 输出要求：
- 分析结果应条理清晰，层次分明
- 使用现代易懂的语言表达专业内容
- 建议要具体可行，避免空泛建议
- 对关键问题要有明确的指导意见

6. 特殊分析维度：
- 2025年乙巳年运势分析
- 流年吉凶预测
- 每月运势变化
- 重大事项选择建议

请基于以上专业素养，为用户提供全面、准确、实用的命理分析。`
        },
        {
          role: "user",
          content: `请根据以下信息进行八字命理分析：
姓名：${formData.value.name}
性别：${formData.value.gender === 'male' ? '男' : '女'}
出生日期时间：${formData.value.birthDate} ${formData.value.birthTime}
出生地点：${formData.value.birthCity}
婚姻状况：${
            {
              'single': '未婚',
              'married': '已婚',
              'divorced': '离异',
              'widowed': '丧偶'
            }[formData.value.maritalStatus]
          }

请提供详细的八字命理分析，包括：

1. 八字命盘分析：
- 年月日时八字排盘
- 十神配置分析
- 五行强弱分析
- 日主特征解读

2. 性格特征分析：
- 先天性格倾向
- 为人处世特点
- 潜在优势
- 需要注意的缺点

3. 事业发展分析：
- 适合的职业方向
- 事业发展建议
- 2025年事业机遇点
- 需要避免的事业陷阱

4. 财运分析：
- 财运特点
- 理财建议
- 2025年财运高峰期
- 破财风险提醒

5. 感情婚姻分析：
- 感情特质
- 桃花运势
- 婚姻建议
- 2025年感情运势

6. 健康分析：
- 易出现的健康问题
- 养生保健建议
- 2025年健康注意事项
- 饮食起居建议

7. 2025年运势：
- 流年吉凶
- 每月运势变化
- 重大事项选择建议
- 开运化解方法`
        }
      ]
    }

    // 调用API
    const response = await fetch('https://api.kksj.org/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer sk-ybt88aCSRjgQ7CG3So1enrNvrRv8JnvJ4fwyW1H0135lxS06'
      },
      body: JSON.stringify(requestData)
    })

    if (!response.ok) {
      throw new Error('网络请求失败，请稍后重试')
    }

    const data = await response.json()
    
    if (data.choices && data.choices[0] && data.choices[0].message) {
      analysisResult.value = data.choices[0].message.content
      showResult.value = true
    } else {
      throw new Error('解析结果格式错误')
    }

  } catch (err) {
    error.value = err.message || '分析失败，请稍后重试'
  } finally {
    isLoading.value = false
  }
}
</script>

<style scoped>
