}

.submit-btn:not(:disabled):hover {
  background-color: #3aa876;
}

.submit-btn:disabled {
  background-color: #a8d5c2;
  cursor: not-allowed;
}

.error-message {
  color: #dc3545;
  font-size: 0.9rem;
  text-align: center;
  padding: 8px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #f5c6cb;
}

/* Mobile-specific styles */
@media (max-width: 480px) {
  .user-info-form {
    padding: 16px;
  }

  input, select {
    padding: 14px;
    font-size: 16px; /* Prevents iOS zoom on focus */
  }

  .submit-btn {
    padding: 16px;
    font-size: 16px;
  }
